import React from "react"
import debounce from "lodash.debounce"

const useSectionList = () => {
  const [sections, setSections] = React.useState({})
  const registerSection = ({ id, node }) =>
    setSections(prevSections => ({ ...prevSections, [id]: node }))

  return { sections, registerSection }
}

const useScrollLocation = sections => {
  const [current, setCurrent] = React.useState(null)

  const setScrollToHash = () => {
    const id = window.location.hash.substring(1)
    if (!sections[id]) return

    sections[id].scrollIntoView()
    setCurrent(sections[id])
  }
  // Run on internal navigation
  if (typeof window !== "undefined") window.onhashchange = setScrollToHash
  // Run on init
  React.useEffect(setScrollToHash)

  const setHashToScroll = () => {
    const topSectionId = Object.keys(sections).find(
      v => sections[v] && sections[v].getBoundingClientRect().top === 0
    )
    if (topSectionId) window.location.hash = `#${topSectionId}`
  }
  const updateScrollLocation = debounce(setHashToScroll, 100)

  return { current, updateScrollLocation }
}

export { useSectionList, useScrollLocation }
